import React, {useContext} from 'react'
//Context
import CityContext from '../../context/CityContext';
//React Router
import {Link} from 'react-router-dom'
import {useLocation} from 'react-router-dom'
//Styles and Animations
import styled from 'styled-components'
import { Jumbotron, Button, Container } from 'react-bootstrap';
import {motion} from 'framer-motion'
import {fadeIn} from '../../animations'

const CityDetails = () => {
//Context
const {  cityFinder } = useContext(CityContext); 
const location = useLocation();
const id = location.pathname.split("/")[2];
const cityObject =  cityFinder(id);

return (

      <DetailStyled variants = {fadeIn} initial='hidden' animate="show">
        <Container>
        <Jumbotron>
          {
          cityObject ? 
          <>
            <h1>{cityObject.name}</h1>
            <h2>Cigarettes smoked per day: <span>{cityObject.cigg}</span></h2>
            <h2>The AQI: <span>{cityObject.aqi}</span></h2>
            <Link to='/'>
            <Button className='button' size="lg" variant="info">
              Back To Home
            </Button>
            </Link>
          </>
          :
          null
        }
        </Jumbotron>
        </Container>
      </DetailStyled>
    );
}

const DetailStyled = styled(motion.div)`
margin-top: 2rem;
h1{
    font-family: 'Montserrat', sans-serif;
   font-weight: 600;
    margin-bottom: 2rem;
  }
h2{
    font-family: 'Montserrat', sans-serif;
    margin-bottom: 1rem;
    font-weight: 500;
   }
.button{
    margin-top: 2rem;
  }
.jumbotron{
  background-color: #e6e6e6 !important;
  border: 1px solid black;
}
span{
     font-weight: 600;
     text-decoration: underline;
   }
`

export default CityDetails
